import webServer from './webServer';
const apiPath = '/api/v1';
export default {
  server: {
    protocal: webServer.protocal,
    host: webServer.host,
    port: webServer.port
  },
  apis: {
    user: `${apiPath}/user`,
    auth: `${apiPath}/auth`,
    blog: `${apiPath}/blog`
  }
}



